@import "fonts";
@import "rouge-github";

h1, h2, h3, h4, h5, h6 {
    color:#222;
    margin:0 0 20px;
}

p, ul, ol, table, pre, dl {
    margin:0 0 20px;
}

h1, h2, h3 {
    line-height:1.1;
}

#name {
    text-transform: uppercase;
    font-size: 22pt;
    font-weight: bold;
    padding-bottom: 10px;
}

#logo {
    border-radius: 50%;
}

h1 {
    text-transform: uppercase;
    font-size: 15pt;
    font-weight: bold;
}

h2 {
    color:#393939;
}

h3, h4, h5, h6 {
    color:#494949;
}

a {
    color:#cb4b16;
    text-decoration:none;
}

.title {
    color:#cb4b16;
    text-decoration:none;
}

.fa:hover, .fa:focus, .ai:hover, .ai:focus{
    color: #b64313;
    text-decoration:none;
}

a:hover, a:focus {
    color: #b64313;
    font-weight: bold;
}

blockquote {
    border-left:1px solid #e5e5e5;
    margin:0;
    padding:0 0 0 20px;
    font-style:italic;
}

code, pre {
    font-family:Monaco, Bitstream Vera Sans Mono, Lucida Console, Terminal, Consolas, Liberation Mono, DejaVu Sans Mono, Courier New, monospace;
    color:#333;
}

pre {
    padding:8px 15px;
    background: #f8f8f8;
    border-radius:5px;
    border:1px solid #e5e5e5;
    overflow-x: auto;
}

table {
    border-collapse:collapse;
}

th, td {
    text-align:left;
    padding:5px 10px;
    border-bottom:1px solid #e5e5e5;
}

dt {
    color:#444;
    font-weight:700;
}

th {
    color:#444;
}

img {
    max-width:100%;
}

.wrapper {
    width: 60%;
    margin: 0px;
    margin-left: 10%;
}

header {
    width:285px;
    float:left;
    position:fixed;
    text-align: center;
    -webkit-font-smoothing: subpixel-antialiased;
}


body {
    background-color: #fff;
    padding-top: 80px;
    font-family: 'Lato';
    font-size: 13pt;
    color:#3e3e3e;
}

section {
    width: 80%;
    float:left;
    margin-left: 350px;
    padding-bottom:50px;
}

#contacts {
    display: inline-block;
    padding-top: 10px;
    font-size: 300%;
}

strong {
    color:#222;
    font-weight:700;
}

small {
    font-size:11px;
}

hr {
    border:0;
    background:#e5e5e5;
    height:1px;
    margin:0 0 20px;
}

footer {
    width:270px;
    float:left;
    position:fixed;
    bottom:50px;
    -webkit-font-smoothing:subpixel-antialiased;
}

#publications, #talks, #service{
    padding-top: 20px;
}

.pubtable {
    width: 100%;
    border-collapse: collapse;
}

.pubtitle {
    text-transform: capitalize;
    font-size: 13pt;
    color:#cb4b16;
}

.pubtd, .pubtdy {
    padding:10px 20px;
    text-align: left;
    border-bottom:2px solid #e5e5e5;
    border-top:2px solid #e5e5e5;
}

.pubtdy {
    width: 10%;
    text-align: center;
}

.publinks {
    font-size: 11pt;
}

.authors, .venue {
    font-size: 11pt;
}

@media print, screen and (max-width: 960px) {

    div.wrapper {
        width:auto;
        margin:0;
    }

    header, section, footer {
        float:none;
        position:static;
        width:auto;
    }

    header {
        padding-right:320px;
    }

    section {
        border:1px solid #e5e5e5;
        border-width:1px 0;
        padding:20px 0;
        margin:0 0 20px;
    }

    header a small {
        display:inline;
    }

    header ul {
        position:absolute;
        right:50px;
        top:52px;
    }
}

@media print, screen and (max-width: 720px) {
    body {
        word-wrap:break-word;
    }

    header {
        padding:0;
    }

    header ul, header p.view {
        position:static;
    }

    pre, code {
        word-wrap:normal;
    }
}

@media print, screen and (max-width: 480px) {
    body {
        padding:15px;
    }
}

@media print {
    body {
        padding:0.4in;
        font-size:12pt;
        color:#444;
    }
}
